import React, { useState, useEffect } from "react";
import "./QuestionCard.css";

const QuestionCard = ({
  question,
  questionNumber,
  totalQuestions,
  chapterNumber,
  chapterName,
  onAnswer,
  onBack,
  onSkip,
  onJump,
}) => {
  const [selected, setSelected] = useState(null);
  const [textValue, setTextValue] = useState("");

  useEffect(() => {
    setSelected(null);
    setTextValue("");
  }, [question]);

  /* -----------------------------------------
     ğŸ”¥ ì˜µì…˜ ë³€í™˜ ë¡œì§ â€” ì™„ì „ ê³ ì • ë²„ì „
  ----------------------------------------- */
  const getOptionList = () => {
    const opts = question.options;

    if (!opts) return [];

    /** 1) ë°°ì—´ í˜•íƒœ â†’ Multi ë¬¸í•­ ì •ìƒ ì²˜ë¦¬ **/
    if (Array.isArray(opts)) {
      // Likertê°€ ë°°ì—´ í•˜ë‚˜ ë¬¸ìì—´ì¸ ê²½ìš°
      if (opts.length === 1 && typeof opts[0] === "string" && opts[0].includes("|")) {
        return opts[0].split("|").map(o => o.trim()).filter(Boolean);
      }
      // Multi: ë°°ì—´ ê·¸ëŒ€ë¡œ ì‚¬ìš© (ê° í•­ëª© í•˜ë‚˜ì”©)
      return opts.map(o => o.toString().trim());
    }

    /** 2) ê°ì²´ í˜•íƒœ â†’ Firestore ë°°ì—´ ëŒ€ì‹  ê°ì²´ ì €ì¥ëœ ê²½ìš° */
    if (typeof opts === "object") {
      return Object.values(opts).map(o => o.toString().trim());
    }

    /** 3) ë¬¸ìì—´ë§Œ ë“¤ì–´ì˜¨ ê²½ìš° */
    if (typeof opts === "string") {
      if (opts.includes("|")) {
        return opts.split("|").map(o => o.trim());
      }
      if (opts.includes(",")) {
        return opts.split(",").map(o => o.trim());
      }
      return [opts.trim()];
    }

    return [];
  };

  const optionList = getOptionList();
  const hasValidOptions = optionList.length > 0;

  const handleOptionClick = (opt, idx) => {
    setSelected(idx);
  };

  const handleNext = () => {
    if (question.type === "text") {
      if (textValue.trim()) {
        onAnswer(textValue);
      }
    } else if (selected !== null) {
      onAnswer(optionList[selected]);
    }
  };

  const isNextEnabled =
    question.type === "text" ? textValue.trim().length > 0 : selected !== null;

  return (
    <div className="question-card">
      {/* Section Header */}
      <div className="qc-section-header">
        <div className="qc-section-left">
          <span className="qc-section-number">Section {chapterNumber}.</span>
          <span className="qc-section-name">{chapterName}</span>
        </div>
        <div className="qc-section-progress">
          {questionNumber} / {totalQuestions}
        </div>
      </div>

      {/* Question Text */}
      <h2 className="qc-question-text">{question.text}</h2>

      {/* TEXT TYPE */}
      {question.type === "text" && (
        <textarea
          className="qc-textarea"
          placeholder="Write your answer..."
          value={textValue}
          onChange={(e) => setTextValue(e.target.value)}
        />
      )}

      {/* MULTI / LIKERT */}
      {(question.type === "multi" || question.type === "likert") && hasValidOptions && (
        <div className="qc-options-wrapper">
          {optionList.map((opt, idx) => (
            <button
              key={idx}
              className={`qc-option-btn ${selected === idx ? "selected" : ""}`}
              onClick={() => handleOptionClick(opt, idx)}
            >
              {question.type === "multi" && (
                <span className="qc-option-number">{idx + 1}</span>
              )}
              <span className="qc-option-text">{opt}</span>
            </button>
          ))}
        </div>
      )}

      {/* Buttons */}
      <div className="qc-button-row">
        <button className="qc-btn back" onClick={onBack}>
          â† Back
        </button>

        <button className="qc-btn skip" onClick={onSkip}>
          â­ Skip
        </button>

        <button className="qc-btn jump" onClick={onJump}>
          â†’ Jump
        </button>

        <button
          className={`qc-btn next ${isNextEnabled ? "active" : ""}`}
          disabled={!isNextEnabled}
          onClick={handleNext}
        >
          Next â†’
        </button>
      </div>
    </div>
  );
};

export default QuestionCard;
